'cryptoAssetsManagement#1.1':'CINDX是一个加密货币资产管理中心。','cryptoAssetsManagement#1.2':'我们致力于为投资者、交易者和开发商解决切实问题。 ','cryptoAssetsManagement#2':'СINDX.MARKET是一个投资者市场','cryptoAssetsManagement#3':'再也不要担心您的知识和经验了 - 加密资产交易的专家在这里随时为您提供帮助','cryptoAssetsManagement#4':'掌控您的加密货币基金：您的代币时刻保存在您的钱包内','cryptoAssetsManagement#5':'只有获利才需付费：只有您的投资组合盈利时才会向您收取费用','cryptoAssetsManagement#6':'CINDX LIVE','cryptoAssetsManagement#7':'CINDX.TRADE是一个交易者终端 ','cryptoAssetsManagement#8':'如果您是一名成功的交易者，您便可在无需投入任何营销费用的情况下吸引资本。','cryptoAssetsManagement#9':'我们为您提供了一个终端、应用商店、市场和其他先进工具，助您成功交易。','cryptoAssetsManagement#10':'CINDX助您将您的声誉和专业知识货币化。您的交易历史和声誉是透明的','cryptoAssetsManagement#11':'CINDX.TALK, CINDX.HUB & CINDX.DAO','cryptoAssetsManagement#12':'CINDX.talk  - 一个有用内容奖励的社交网络','cryptoAssetsManagement#13':'CINDX.hub - 包括供应商的帐户及用于测试和集成第三方软件的枢纽','cryptoAssetsManagement#14':'CINDX.loan - 一个允许提供和投资加密支持P2P贷款的组件','cryptoAssetsManagement#15':'CINDX.market 是一个为投资者而设的市场，投资者可以评估和选择要遵循的策略或投资的指数。','cryptoAssetsManagement#16':'CINDX.trade 是交易员可在一个地方交易不同交易所加密资产、分析市场、自动交易和实现突出收益的模块','cryptoAssetsManagement#17':'CINDX使用API来收集数据和执行交易。API和密钥的安全性绝不低于用户的登录名和密码详细信息。为了保护CINDX用户，我们使用银行级加密将所有秘密API密钥保存在独立的数据库中。','cryptoAssetsManagement#18':'CINDX.HUB','cryptoAssetsManagement#19':'CINDX.hub是一个用于创建算法策略、历史测试和分析的组件，其中包含许多现成的解决方案。','cryptoAssetsManagement#20':'HUB.Pro是专业交易者的选择，它允许自动交易策略进行全天候交易和收益。','cryptoAssetsManagement#21':'HUB.Lite是投资者的选择，他们可以使用灵活风险设置支持的成熟工具。','cryptoAssetsManagement#22':'','cryptoAssetsManagement#23':'','cryptoAssetsManagement#24':'','teamLegal#1':'我们拥有优秀的团队和法律部门',//   'teamLegal#2': '法律和监管组织框架',
