'cryptoAssetsManagement#1.1':'CINDX는 암호화 자산 관리 허브입니다. 투자자, 트 ,','cryptoAssetsManagement#1.2':'트레이더, 개발자가 가지고 있는 문제를 해결합니다.','cryptoAssetsManagement#2':'투자자를 위한 CINDX.MARKETPLACE ','cryptoAssetsManagement#3':'지식과 경험은 필요치 않습니다. 암호 화폐 거래 전문가들이 당신을 돕기 위해 기다리고 있습니다. ','cryptoAssetsManagement#4':'암호화 자산을 관리하십시오: 코인은 언제나 지갑 속에서 보관되어 있습니다.','cryptoAssetsManagement#5':'수익을 올린 경우에만 지불하십시오: 당신의 포트폴리오가 수익을 낼 때에만 수수료가 부과됩니다.','cryptoAssetsManagement#6':' CINDX LIVE','cryptoAssetsManagement#7':'트레이더를 위한 CINDX.TERMINAL ','cryptoAssetsManagement#8':'성공적인 트레이더라면 마케팅을 하지 않고도 자본을 유지할 수 있습니다. ','cryptoAssetsManagement#9':'터미널, 앱스토어, 마켓 플레이스 및 거래를 성공시키는 데 도움이 되는 기타 고급 도구들을 제공합니다 ','cryptoAssetsManagement#10':'CINDX는 당신의 명성과 전문성으로 수익을 창출하는 데에 도움이 됩니다. 당신의 거래 내역과 영향력은 명백하게 게시됩니다 ','cryptoAssetsManagement#11':'CINDX.TALK, CINDX.HUB & CINDX.DAO','cryptoAssetsManagement#12':'CINDX.talk - 유용한 콘텐츠에 대한 보상을 제공하는 소셜 네트워크t','cryptoAssetsManagement#13':'CINDX.hub - 구성 요소에는 공급 업체의 계정뿐만 아니라 타사 소프트웨어 테스트 및 통합을위한 허브가 포함됩니다','cryptoAssetsManagement#14':'CINDX.loan - 암호화 통화로 P2P 대출을 제공하고 투자 할 수있는 구성 요소','cryptoAssetsManagement#15':'CINDX.market 투자자를위한 시장으로, 따라야 할 전략이나 투자 지표를 평가하고 선택할 수 있습니다..','cryptoAssetsManagement#16':'CINDX.trade 한 곳에서 서로 다른 거래소에서 암호 거래를 거래하고, 시장을 분석하고, 거래를 자동화하고, 뛰어난 이익을 달성하는 거래자를위한 모듈입니다.','cryptoAssetsManagement#17':'CINDX 데이터 수집 및 거래 실행을 위해 API를 사용합니다. API 및 비밀 키는 사용자의 로그인 및 비밀번호 세부 정보보다 안전합니다. CINDX 사용자를 보호하기 위해 모든 비밀 API 키를 뱅크 수준 암호화를 사용하여 별도의 데이터베이스에 보관합니다.','cryptoAssetsManagement#18':'CINDX.HUB','cryptoAssetsManagement#19':'CINDX.hub 바로 사용할 수있는 솔루션으로 알고리즘 전략, 내역 테스트 및 분석을 생성하기위한 구성 요소입니다.','cryptoAssetsManagement#20':'HUB.Pro 24/7 거래 및 수입을위한 거래 전략을 자동화 할 수있는 전문 상인을위한 옵션입니다.','cryptoAssetsManagement#21':'HUB.Lite 유연한 위험 설정으로 뒷받침 된 검증 된 도구로 작업 할 수있는 투자자를위한 옵션입니다.','cryptoAssetsManagement#22':'','cryptoAssetsManagement#23':'','cryptoAssetsManagement#24':'','teamLegal#1':'우리는 최고의 실력과 팀워크를 가진 팀을 갖고 있습니다',//   'teamLegal#2': '법률 및 규정의 체계적.',
