'problemSolving#1':'시간 ','problemSolving#2':'신뢰 ','problemSolving#3':'불확실성','problemSolving#4':'암호화폐에 투자할 돈은 있지만, 내 포트폴리오를 관리할 수 있는 전문 트레이더를 검색하고 연락할 시간이 없습니다.','problemSolving#5':'수익을 내기 위해 다른 사람에게 자금을 맡기는 것에 대해 항상 불편하게 생각합니다. ','problemSolving#6':'암호화 거래를 둘러싸고 있는 기술은 사용자 친화적이지 않습니다. 어떻게 코인을 거래하는지 모릅니다.','problemSolving#7':'CINDX는 당신을 신뢰 가능하며 호평을 받는 트레이더와 연결해드립니다.  ','problemSolving#8':'THE CINDX 플랫폼에서 당신의 자금은 지갑에 계속 남아 있습니다. ','problemSolving#9':'시장에서 가장 뛰어난 트레이더와 함께 수익을 냅니다.','problemSolving#10':'CINDX의 트레이더는 거래 성공 내역에 따라 등급을 평가 받습니다. 자신에게 맞는 트레이더를 선택하십시오.','problemSolving#11':'탈중앙화된 시스템 및 스마트 계약을 통해 자금을 트레이더에게 보내지 않고 관리할 수 있습니다.','problemSolving#12':'CINDX의 모든 투자자는 시장에서 가장 성공적인 트레이더가 자금을 관리할 수 있는 기회를 갖습니다.','problemSolving#13':'CINDX가 투자자들을 위해 해결하는 문제들은 어떤 게 있을까요?','problemSolving#14':'CINDX LIVE','howCindxWorks#1':'투자자 ','howCindxWorks#2':'트레이더 ','howCindxWorks#3':'판매자 ','howCindxWorks#4':'뛰어난 트레이더의 포트폴리오 관리 기술을 통해 수입을 증가시킵니다.','howCindxWorks#5':'실적 및 거래 전략에 기반해 투자자로부터 수수료를 받습니다.','howCindxWorks#6':'트레이드 봇, 뉴스 위젯과 같은 고급 트레이딩 도구를 판매합니다.','howCindxWorks#7':'커미션과 구독 수수료를 벌어보세요.','howCindxWorks#8':'거래하고자 하는 암호 자산을 가지고 CINDX에 참여합니다.','howCindxWorks#9':'투자자에게 도움이 되는 트레이딩 전문 지식을 가지고 CINDX에 참여합니다.','howCindxWorks#10':'투자자를 도울 수 있는 트레이딩 앱을 가지고 CINDX에 참여합니다.','howCindxWorks#11':'거래를 쉽고, 안전하며, 수익성 있게 만들기 위해 노력해왔습니다.','howCindxWorks#12':'CINDX의 작동 원리','marketAnalytics#1':'시장 분석','marketAnalytics#2':'관리중인 암호화 자산의 성장 (수백만, 미화)','marketAnalytics#3':'지역별 온라인 거래자 수 (천명, 명)','marketAnalytics#4':'암호 통화 시장의 월간 거래량 (에 대한 수십억, 미화)','marketAnalytics#5':'2018 년에는 암호화 통화 가격이 전반적으로 하락하는 추세를 보였으 나 펀드 관리를 통해 새로운 유동성이 자산으로 유입되면서 업계는 계속 발전했습니다.','marketAnalytics#6':'','marketAnalytics#7':'2017 년과 2018 년의 지역별 인종 차별에 의한 온라인 상인 수의 증가. 2018 년의 지도자는 아시아와 유럽이었으며 주로 새로운 거래 참가자에 대한 진입 문턱이 낮은 암호 거래의 출현으로 인해 발생했습니다.','marketAnalytics#8':'',"marketAnalytics#_9":'암호화 시장에서의 거래량은 이미 암호화 통화가 과장된 2017 년 말의 거래량을 이미 초과했습니다. 거의 매년 곰 시장에도 불구하고, 연구 데이터로 표시된 바와 같이 거래는 매우 활발하게 발전하고 있습니다.',"marketAnalytics#_10":''