'feedbackProject#18':'Austin Kimm','feedbackProject#19':'CINDX 고문','feedbackProject#20':'국제 금융 서비스 CEO','feedbackProject#21':'비디오','feedbackProject#22':'"CINDX는"상인 - 투자자 "관계를 다시 개인화합니다."','feedbackProject#23':'Amarpreet Singh','feedbackProject#24':'CINDX 고문','feedbackProject#25':'글로벌 블록 체인 재단 고문','feedbackProject#26':'비디오','feedbackProject#27':'“CINDX는 모든 투자자와 거래자를위한 정교한 도구를 제공 할뿐만 아니라 플랫폼도 제공합니다. 우리는 상인들의 경험을 복사 할 수 있습니다. "','feedbackProject#28':'Keith Teare','feedbackProject#29':'CINDX 고문','feedbackProject#30':'Accelerated Digital Ventures 설립자 겸 집행 위원장','feedbackProject#31':'비디오','feedbackProject#32':'"경험 많은 상인의 추적자. CINDX는 경험이 없다는 피할 수없는 두려움을 제거합니다. "',//'feedbackProject#33': 'Mansur Rasulov',
