'feedbackProject#36':'Sadie Hutton','feedbackProject#37':'CINDX 고문','feedbackProject#38':'AuBit 창립자이자 CSO','feedbackProject#39':'비디오','feedbackProject#40':'"전체 프로토콜은 참여하는 모든 사람들을위한 진정한 프로토콜입니다."','feedbackProject#41':'Graham Doggart','feedbackProject#42':'CINDX 고문','feedbackProject#43':'AuBit 창립자이자 CSO','feedbackProject#44':'비디오','feedbackProject#45':'"투자자가 자신의 펀드를이 시장의 전문가 인 누군가에게 맡기고 자금을 통제하지 않고 거래 할 수 있다고 상상해보십시오."',//'feedbackProject#46': 'Alina Vardanian',
