'teamAdvisersPartners#170':'Leonard는 벤처 캐피털 자금 조달, 자금 구조화, 기업 실사(펀드와 LP 모두를 대표)에 10년 이상의 경험을 보유하고 있습니다. Leonard 는 모든 기업가들에게 조언합니다 - 트렌드를 따르지 말고, 본인의 관심사를 따르세요.','teamAdvisersPartners#171':'Kosol Saisanit','teamAdvisersPartners#172':'Power C Technology Co., Ltd, C Power Solutions Co., Ltd 및 CoachHub 설립자','teamAdvisersPartners#173':'경영 전문가 및 마케팅 전문가','teamAdvisersPartners#174':'동남아시아 최대의 FMCG 브랜드 매니저','teamAdvisersPartners#175':'씨 Saisanit 대학교 태국 가정대학에서 MBA 를 Executive management 및 BBA 주요 마케팅이다.','teamAdvisersPartners#176':'그 이전에 개최되는 위치의 브랜드 관리자의 몽드 Nissin,가장 큰 중 하나의 선수 FMCG 시장에 남쪽으로 아시아 지역입니다. 또한,그가 광범위한 경력에서 부동산 서비스 회사,CB Richard Ellis.','teamAdvisersPartners#177':'그의 회사,C 전원 기술 Co., 주식 회사,에너지 서비스 회사(ESCO)전문으로 줄여 에너지 손실과 전력품질 향상을 통해 혁신적인 기술입니다. C 전원 솔루션 Co., 주식 회사는 온라인 마케팅 지점을 지원,클라이언트에 온라인 브랜딩 전략적 계획,마케팅 커뮤니케이션을 계획하고 관리자에서 마케팅 및 판매 계획입니다.','teamAdvisersPartners#178':'Teo Lincoln','teamAdvisersPartners#179':'Intel Wise Group의 창립자 겸 CEO','teamAdvisersPartners#180':'DP Information Group 전 전무 이사','teamAdvisersPartners#181':'브루나이 중앙 은행 고문 브루나이 다루 살람 (AMBD)','teamAdvisersPartners#182':'링컨은 Intel Wise Group의 창립자이자 CEO입니다.','teamAdvisersPartners#183':'이에 앞서,그는 최고 운영 책임자 및 관리 감독의 DP 정보를 그룹의 자회사–의 plc 그리고 관리되는 여러 데이터 저장소는 동남 아시아에서(포함하여 싱가포르,말레이시아,인도네시아,베트남,브루나이).','teamAdvisersPartners#184':'링컨의 경험을 포함한 위험 관리 및 IT 인프라와 미국의 해외 은행의 그룹 스탠다드차타드은행. 통계로,그가 도입 신용 득점을 여러 개의 은행에서 아시아-태평양하고 또한 찾을 봉사하는 시간에 프로젝트 팀은 세계 은행의 그룹과 아시아 개발 은행합니다. 인텔','teamAdvisersPartners#185':'우산 아래의 충칭의 연결 이니셔티브,싱가포르 중국 정부는 양자 프로젝트에 인텔명을 기획하고있는 데이터에서 100 개 이상의 백만 회사는 중국에서 도와 전 세계의 유명한 기업의 규정 준수함으로 필요합니다. 더 최근에,회사에 착수했 blockchain 기술 개발 플랫폼에 대한 금융서비스 거래의 디지털 자산입니다. 또한,링컨은 또한 고문 브루나이의 중앙 은행 Autoriti Monetari 브루나이 다루살람(AMBD).','teamAdvisersPartners#186':'또한,링컨은 또한 고문 브루나이의 중앙 은행 Autoriti Monetari 브루나이 다루살람(AMBD).','teamAdvisersPartners#187':'Reinhard Berger','teamAdvisersPartners#188':'Reinhard Berger는 혁신적인 DLT 기술에 중점을 둔 VC 회사 인 Pecunio Blockchain Technologies의 CEO입니다.','teamAdvisersPartners#189':'Accenture의 Business Architect로 Credit Suisse, UBS 및 Invesco를 컨설팅했습니다. capgemini의 교장이었습니다.','teamAdvisersPartners#190':'관$250M 에서 대체 자산을 통해 투자 금융 AG2003 년 이후','teamAdvisersPartners#191':'그분을 보유하고 MSc 에서 컴퓨터 과학,M. 는 법률에,뿐만 아니라에서 MBA 를 다뉴브 대학교도 있습니다.','teamAdvisersPartners#192':'하기 전에는 금전,라인하르트의 경력으로 시작업 건축가를 위한 글로벌 컨설팅 회사인 액센츄어,클라이언트와 같은 크레딧 스위스 UBS 및 Invesco.','teamAdvisersPartners#193':'그 다음으로 봉사자 프랑스의 기술 거 캡 제미니. 2003 년에,Reinhard 공동 설립 대체 투자 금융 AG,독립 헤지 펀드,연산자는 그는 대안을 초과하는 자산$250M.','teamAdvisersPartners#194':'직관적인 지도자,자연적인 멘토이자 진정한 전략가 Pecuino 의 투구.','teamAdvisersPartners#195':'','teamAdvisersPartners#196':'','teamAdvisersPartners#197':'',//    'joinTokenPreSale#1': '토큰 프리세일 참여하기!',
